import React from "react";
import {projects} from '@portfolio/shared/sections/portfolio';
import './portfolio.css';
import Carousel from "src/components/carousel/Carousel";
import Timeline from "src/components/timeline/Timeline";
import TimelineElement from "src/components/timeline-element/TimelineElement";
import {HiOutlineExternalLink} from "react-icons/hi";
import StarsCanvas from "src/canvas/stars/Stars";
import EarthCanvas from "src/canvas/earth/Earth";


const Portfolio = () => {

  const portfolioTitles = ['JustTravel', 'MovieReviewer', 'GymApp', 'Portfolio v1', 'Portfolio v2', 'Portfolio v3', 'Portfolio v4', 'Portfolio v5'];
  
  return (
    <section id="portfolio" className="portfolio">

      <StarsCanvas />
      
      <div className="section-header">
        <h5 className="color-primary-text-dark">Moje projekty</h5>
        <h2 className="color-secondary">Portfolio</h2>
      </div>

      <Timeline>

        {projects.filter((p, index, self) => portfolioTitles.includes(p.title+'') && index === self.findIndex((t) => (t.title === p.title))).map((project, index) => {
          return (
            <TimelineElement
              key={project.title}
              description={project.technology}

              icon={['Portfolio', 'GymApp'].some(t => project.title.includes(t))
                ? <img src="react.png" alt="React logo"/>
                : <img src="angular.png" alt="Angular logo"/> 
              }
              iconBg={"var(--color-primary)"}
              
              contentStyle={{ 
                background: "var(--color-primary-dark)", 
                color: "var(--color-primary-text)",
                boxShadow: "0 0 15px var(--color-secondary)"
              }}
            >

              <div className="portfolio-card">

                <h2 className="color-secondary">
                  {project.title}{" "}
                  {project?.link && 
                    <a className="color-secondary" href={project?.link} target='_blank' rel="noreferrer">
                      <HiOutlineExternalLink/>
                    </a>
                  }
                </h2>
                <p className="color-primary-text-dark">{project.subtitle}</p>
                {/* <h4 className="color-primary-text">{project.technology}</h4> */}

                <Carousel images={project.images}/>

              </div>

            </TimelineElement>
          )
        })}

      </Timeline>

      <div className="earth-container">
        <EarthCanvas />
      </div>
      
    </section>
  )
}

export default Portfolio;