import React, {useEffect, useRef, useState} from "react";
import './timeline-element.css';


type Props = {
  children: any;
  description: string;
  icon: React.ReactNode;
  iconBg: string;
  contentStyle: any;
};

const TimelineElement = ({children, description, icon, iconBg, contentStyle}: Props) => {

  const ref = useRef<HTMLDivElement>(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const el = ref.current;
    if (!el) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            setTimeout(() => {
              setIsVisible(true);
            }, 100);
          }
          else {
            setIsVisible(false);
          }
        });
      },
      {
        root: null,
        rootMargin: '0px',
        threshold: 0.2,
      }
    );
    observer.observe(el);
    return () => {
      observer.disconnect();
    };
  }, []);


  return (
    <div ref={ref} className={`timeline-element ${isVisible && 'visible'}`}>
      
      <div className="timeline-icon" style={{ background: iconBg }}>
        <div className="icon-wrapper">
          {icon}
        </div>
      </div>
      
      {description && <div className="timeline-description">{description}</div>}
      
      <div className="timeline-content" style={contentStyle}>
        <div className="content-arrow" />
        {children}
      </div>
      
    </div>
  );
};

export default TimelineElement;
