import React from 'react';
import './carousel.css';
import {Carousel} from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";


type Props = {
  images: any[],
};

const CarouselComponent = ({images}: Props) => {
  
  return (
    <div className="carousel-container">
      <Carousel
        showStatus={false} 
        showThumbs={false} 
        autoPlay={true} 
        infiniteLoop={true}
        interval={4000}
        transitionTime={1000}
        stopOnHover={true}
        swipeable={true}
        emulateTouch={true}
      >
        {images.map(i => {
          return (
            <div className="carousel-image-container">
              <img src={i} alt={i} className='carousel-image' key={i}/>
            </div>
          )
        })}
      </Carousel>
    </div>
  )
}

export default CarouselComponent;